/* =====================================================
   Fractal UI styles
   ===================================================== */

@import 'config/**/*.css';
@import 'docs/**/*.css';





/*  */

@font-face {
  font-family: roboto-light;
  src: url(../fonts/roboto-light.eot?);
  src: local(roboto-light), url(../fonts/roboto-light.eot?#iefix) format("embedded-opentype"),url(../fonts/roboto-light.woff2) format("woff2"), url(../fonts/roboto-light.woff) format("woff"), url(../fonts/roboto-light.ttf) format("truetype"), url(../fonts/roboto-light.svg#roboto-light) format("svg")
}

@font-face {
  font-family: roboto-medium;
  src: url(../fonts/roboto-medium.eot?);
  src: local(roboto-medium), url(../fonts/roboto-medium.eot?#iefix) format("embedded-opentype"),url(../fonts/roboto-medium.woff2) format("woff2"), url(../fonts/roboto-medium.woff) format("woff"), url(../fonts/roboto-medium.ttf) format("truetype"), url(../fonts/roboto-medium.svg#roboto-medium) format("svg")
}

@font-face {
  font-family: roboto-regular;
  src: url(../fonts/roboto-regular.eot?);
  src: local(roboto-regular), url(../fonts/roboto-regular.eot?#iefix) format("embedded-opentype"),url(../fonts/roboto-regular.woff2) format("woff2"), url(../fonts/roboto-regular.woff) format("woff"), url(../fonts/roboto-regular.ttf) format("truetype"), url(../fonts/roboto-regular.svg#roboto-regular) format("svg")
}

@font-face {
  font-family: roboto-bold;
  src: url(../fonts/roboto-bold.eot?);
  src: local(roboto-bold), url(../fonts/roboto-bold.eot?#iefix) format("embedded-opentype"),url(../fonts/roboto-bold.woff2) format("woff2"), url(../fonts/roboto-bold.woff) format("woff"), url(../fonts/roboto-bold.ttf) format("truetype"), url(../fonts/roboto-bold.svg#roboto-bold) format("svg")
}

@font-face {
  font-family: "Flaticon";
  src: url("../fonts/flaticon.eot");
  src: url("../fonts/flaticon.eot?#iefix") format("embedded-opentype"),
  url("../fonts/flaticon.woff2") format("woff2"),
  url("../fonts/flaticon.woff") format("woff"),
  url("../fonts/flaticon.ttf") format("truetype"),
  url("../fonts/flaticon.svg#Flaticon") format("svg");
  font-weight: normal;
  font-style: normal;
}
/*  */




/* Theme overrides */
a {
  color: $color-link;

  &:hover {
    color: $color-link--hover;
  }

  &:active {
    color: $color-link--active;
  }
}

.Pen-previewLink svg {
  fill: currentColor;
}

.Header {
  background-color: map(colors, primary, blue);
  color: map(colors, primary, white);
}

.Prose {
  tr td:first-child {
    min-width: 12em;
    padding-right: 1em;
    white-space: nowrap;
  }

  tr td:last-child {
    width: 100%;
  }
}
